import { deepCloneData } from "../../..";
/**
 * 
 * 
 * 
 *  
 *      ,
 */
export var withPoint = function (editor) {
    var e = editor;
    e.setNoSelectionData = function (res) {
        e.noSelection = res
            ? {
                selection: deepCloneData(res.selection),
                type: res.type,
            }
            : undefined;
    };
    return e;
};
