import { service } from "./interceptor";
import { loginBaseUrl, authBaseUrl } from "./mainDomain";
import { appParams } from "./redirectDomain";
/**
 * send first entry request
 */
export var firstEntryCheck = function (data) {
    return service({
        method: "post",
        url: "".concat(loginBaseUrl, "/jwt?").concat(appParams),
        data: data,
    });
};
/**
 * check session state
 */
export var sessionStateCheck = function () {
    return service({
        method: "post",
        url: "".concat(authBaseUrl, "/session/status"),
    });
};
