export var initDate = function (year, month) {
    if (year && month) {
        return {
            year: year,
            month: month,
        };
    }
    else {
        var date = new Date();
        return {
            year: date.getFullYear(),
            month: date.getMonth() + 1,
        };
    }
};
/**
 *
 * @param {Date | undefined} time
 * @returns {DateInfoProps | null}
 */
export var getDateInfo = function (time) {
    if (time) {
        var h = time.getHours();
        return {
            year: time.getFullYear(),
            month: time.getMonth() + 1,
            day: time.getDate(),
            hour: h > 11 ? h - 12 : h,
            unit: h > 11 ? "PM" : "AM",
        };
    }
    return null;
};
