var getLanguageByGeolocation = function () {
    return new Promise(function (resolve, reject) {
        var geolocation = window.navigator.geolocation;
        if (!geolocation) {
            reject(null);
        }
        var onSuccess = function (position) {
            var pos = {
                lng: position.coords.longitude.toFixed(6),
                lat: position.coords.latitude.toFixed(6),
            };
            codegrid.getCode(Number(pos.lat), Number(pos.lng), function (err, code) {
                if (!err) {
                    /**
                     * code maybe has The following results
                     *   cn:hk , cn:mo
                     * Filter strings retain only countries
                     */
                    switch (code.replace(/:\w+/, function () { return ""; })) {
                        case "tw":
                        case "cn":
                            code = "cn";
                            break;
                        default:
                            code = "en";
                    }
                    resolve(code);
                }
            });
        };
        var onError = function () {
            resolve(getLanguageByBrowser());
        };
        geolocation.getCurrentPosition(onSuccess, onError, {
            enableHighAccuracy: true,
        });
    });
};
