var getLanguageByBrowser = function () {
    var languageAndArea = window.navigator.language;
    var language = languageAndArea.split("-")[0];
    switch (language) {
        case "zh":
            return "cn";
        case "en":
            return "en";
        default:
            return "cn";
    }
};
export var getLanguage = function () {
    return getLanguageByGeolocation();
};
