export var withImages = function (editor) {
    var isVoid = editor.isVoid, isInline = editor.isInline;
    editor.isVoid = function (element) {
        return element.type && ["image", "voidText"].includes(element.type)
            ? true
            : isVoid(element);
    };
    editor.isInline = function (element) {
        return element.type && ["image", "voidText"].includes(element.type)
            ? true
            : isInline(element);
    };
    return editor;
};
