/**
 *
 * @param {number} start
 * @param {number} end
 * @returns {Label[]}
 */
export var makeArr = function (start, end) {
    var arr = [];
    for (var i = start; i < end; i++) {
        arr.push({
            id: i,
            content: i.toString(),
        });
    }
    return arr;
};
