/**
 *
 * @param {Array<T>} arr 
 * @param { number } _old 
 * @param { number } _new 
 * @returns {Array<T>} 
 */
export var changeArrData = function (arr, _old, _new) {
    var newArr = [];
    for (var i = 0; i < arr.length; i++) {
        if (i === _old) {
            newArr.push(arr[_new]);
        }
        else if (i === _new) {
            newArr.push(arr[_old]);
        }
        else {
            newArr.push(arr[i]);
        }
    }
    return newArr;
};
