/**
 *
 * @param {string} cssText
 * @returns
 */
export var parseStyleText = function (cssText) {
    var res = {};
    cssText.split(";").forEach(function (item) {
        if (item) {
            var tmp = item.split(":");
            if (tmp.length > 1) {
                var key = tmp[0].trim();
                var val = tmp[1].trim();
                key = key.replace(/-[a-z]/gi, function (regVal) {
                    return regVal.slice(1, 2).toUpperCase();
                });
                res[key] = val;
            }
        }
    });
    return res;
};
/**
 *
 * @param {HTMLElement} el
 * @param {React.CSSProperties} css
 * @returns {void}
 */
export var setStyle = function (el, css) {
    el.removeAttribute("style");
    Object.assign(el.style, css);
    if (!el.getAttribute("style")) {
        el.removeAttribute("style");
    }
};
