/**
 * 
 * 16
 */
export var encode = function (res) {
    if (res) {
        var enStr = "";
        for (var i = 0; i < res.length; i++) {
            enStr += "--u".concat(res.charCodeAt(i).toString(16));
        }
        return enStr;
    }
    return res;
};
/**
 *16
 */
export var decode = function (res) {
    if (res.includes("--u")) {
        var deStr = "";
        var arr = res.split("--u");
        for (var i = 0; i < arr.length; i++) {
            deStr += String.fromCharCode(parseInt("".concat(parseInt(arr[i], 16)), 10));
        }
        return deStr;
    }
    return res;
};
