import { fiberKey } from "../../../Dropdown/Kite/Unit/findDomNode";
/**
 *
 * @param {Element} el
 * @returns {Fiber|null}
 */
export var getElFiber = function (el) {
    var keys = Object.keys(el);
    var key = "";
    for (var i = 0; i < keys.length;) {
        var item = keys[i];
        if (item.includes(fiberKey)) {
            i = keys.length;
            key = item;
        }
        else {
            ++i;
        }
    }
    return key
        ? el[key]
        : null;
};
export var findFiber = function (el, keys) {
    var fiber = getElFiber(el);
    var status = false;
    while (!status && fiber) {
        if (fiber.key && keys.includes(fiber.key)) {
            status = true;
        }
        else {
            fiber = fiber.return;
        }
    }
    return status;
};
