/**
 *
 * @param { () => void } fn
 * @param {{ current: number | null }} timer
 * @param { number } delay
 * @returns {Promise<void> }
 */
export var nextFrame = function (fn, timer, delay) {
    if (delay === void 0) { delay = 1; }
    var count = 0;
    var clock = function (frameFn) {
        timer.current && window.cancelAnimationFrame(timer.current);
        timer.current = window.requestAnimationFrame(function () {
            ++count;
            if (count >= delay) {
                frameFn();
            }
            else {
                clock(frameFn);
            }
        });
    };
    return new Promise(function (resolve) {
        clock(function () {
            var result = fn();
            resolve(result);
        });
    });
};
export var delayFn = function (fn, timer, ms) {
    if (ms === void 0) { ms = 1; }
    var clock = function (frameFn) {
        timer.current && window.clearTimeout(timer.current);
        timer.current = window.setTimeout(frameFn, ms);
    };
    return new Promise(function (resolve) {
        clock(function () {
            var result = fn();
            resolve(result);
        });
    });
};
