/**
 *
 * @param {string|undefined} name
 * @returns {string}
 */
export var createHash = function (name, length) {
    if (length === void 0) { length = 10; }
    var l = 10;
    var psList = window.crypto.getRandomValues(new Uint32Array(l));
    var val = "";
    for (var i = 0; i < l; i++) {
        var v = window
            .encodeURIComponent(window.encodeURI(window.btoa(String(psList[i]))))
            .replace(/[^0-9a-z_-]/gi, "");
        var center = v.length / 2;
        var start = Math.floor(Math.random() * center);
        var end = Math.floor(Math.random() * center + (center - 1));
        val += v.slice(start, end);
    }
    var value = "";
    for (var i = 0; i < length; i++) {
        var index = Math.floor(Math.random() * val.length - 1);
        value += val.slice(index, index + 1);
    }
    value += "_".concat(Date.now().toString(36));
    if (name) {
        return "".concat(name, "-").concat(value);
    }
    return value;
};
