export var setDateMap = function (selectedYear, selectedMonth) {
    // current month;
    var totalDayData = new Date(selectedYear, selectedMonth, 0);
    var totalDay = totalDayData.getDate();
    var arr = [];
    var endWeek = totalDayData.getDay();
    var startWeek = endWeek - (totalDay % 7) + 1;
    if (startWeek < 0) {
        startWeek += 7;
    }
    for (var i = 1; i <= totalDay; i++) {
        arr.push({
            year: selectedYear,
            month: selectedMonth,
            date: i,
            active: true,
        });
    }
    var currentVal;
    // pre month ;
    if (startWeek) {
        var preData = { month: selectedMonth - 1, year: selectedYear };
        if (preData.month < 1) {
            preData.year = selectedYear - 1;
            preData.month = 12;
        }
        currentVal = new Date(preData.year, preData.month, 0).getDate();
        for (var i = 0; i < startWeek; i++) {
            arr.unshift({
                year: preData.year,
                month: preData.month,
                date: currentVal,
                active: false,
            });
            --currentVal;
        }
    }
    // next month
    currentVal = 1;
    var nextData = { month: selectedMonth + 1, year: selectedYear };
    if (nextData.month > 12) {
        nextData.year = selectedYear + 1;
        nextData.month = 1;
    }
    for (var i = arr.length; i < 6 * 7; i++) {
        arr.push({
            year: nextData.year,
            month: nextData.month,
            date: currentVal,
            active: false,
        });
        ++currentVal;
    }
    var dataMap = [];
    var index = -1;
    for (var i = 0; i < arr.length; i++) {
        if (i % 7) {
            dataMap[index].push(arr[i]);
        }
        else {
            ++index;
            dataMap[index] = [arr[i]];
        }
    }
    return dataMap;
};
