/**
 * @file defaultAttr
 * @date 2022-01-14
 * @author xuejie.he
 * @lastModify xuejie.he 2022-01-14
 */
import { getScrollValue } from "../../../Dropdown/Kite/Unit/getScrollValue";
export var defaultAttr = function (p, d, rootEl) {
    var triangle = undefined;
    var offset = {};
    var scrollData = getScrollValue();
    switch (d) {
        case "horizontal":
            triangle = {
                width: "0.5rem",
                height: "1rem",
                color: "rgba(0,0,0,0.7)",
            };
            if (p.endsWith("t")) {
                triangle.offset = {
                    y: function (value, data) { return (data.root / 2 - 13 - data.triangle > 0 ? 13 : value); },
                };
                offset = {
                    y: function (value, data) {
                        if (data.root / 2 - 13 - data.triangle > 0) {
                            return value;
                        }
                        var rect = rootEl.getBoundingClientRect();
                        var val = rect.top + rect.height / 2 + scrollData.y - data.triangle / 2 - 13;
                        return val;
                    },
                };
            }
            else if (p.endsWith("b")) {
                triangle.offset = {
                    y: function (value, data) {
                        return data.root / 2 - 13 - data.triangle > 0
                            ? data.kite - 13 - data.triangle
                            : value;
                    },
                };
                offset = {
                    y: function (value, data) {
                        if (data.root / 2 - 13 - data.triangle > 0) {
                            return value;
                        }
                        var rect = rootEl.getBoundingClientRect();
                        var val = rect.top +
                            rect.height / 2 +
                            scrollData.y +
                            data.triangle / 2 +
                            13 -
                            data.kite;
                        return val;
                    },
                };
            }
            break;
        case "vertical":
            triangle = {
                width: "1rem",
                height: "0.5rem",
                color: "rgba(0,0,0,0.7)",
            };
            if (p.startsWith("l")) {
                triangle.offset = {
                    x: function (value, data) { return (data.root / 2 - 13 - data.triangle > 0 ? 13 : value); },
                };
                offset = {
                    x: function (value, data) {
                        if (data.root / 2 - 13 - data.triangle > 0) {
                            return value;
                        }
                        var rect = rootEl.getBoundingClientRect();
                        var val = rect.left + rect.width / 2 + scrollData.x - data.triangle / 2 - 13;
                        return val;
                    },
                };
            }
            else if (p.startsWith("r")) {
                triangle.offset = {
                    x: function (value, data) {
                        return data.root / 2 - 13 - data.triangle > 0
                            ? data.kite - 13 - data.triangle
                            : value;
                    },
                };
                offset = {
                    x: function (value, data) {
                        if (data.root / 2 - 13 - data.triangle > 0) {
                            return value;
                        }
                        var rect = rootEl.getBoundingClientRect();
                        var val = rect.left +
                            rect.width / 2 +
                            scrollData.x +
                            data.triangle / 2 +
                            13 -
                            data.kite;
                        return val;
                    },
                };
            }
            break;
    }
    return {
        triangle: triangle,
        offset: offset,
    };
};
