/**
 * @file remove class
 * @date 2021-11-26
 * @author xuejie.he
 * @lastModify xuejie.he 2021-11-26
 */
/**
 *
 * @param {HTMLElement} el
 * @param {string | string[]} cls
 * @param {string[]} container
 * @param {string | string[]|undefined} ignoreClass
 */
export var removeClass = function (el, cls, container) {
    var clString = el.getAttribute("class") || "";
    var cur = clString.split(" ");
    var val = cls.trim();
    if (val) {
        var n = container && container.findIndex(function (item) { return item === val; });
        if (Number(n) >= 0) {
            container && container.splice(n, 1);
        }
        n = cur.findIndex(function (item) { return item === val; });
        if (n >= 0) {
            cur.splice(n, 1);
        }
    }
    var curStr = cur.join(" ").trim();
    if (curStr) {
        el.setAttribute("class", curStr);
    }
    else {
        el.removeAttribute("class");
    }
};
