/**
 * 
 * maxLength 
 */
import { Node } from "slate";
import { getLength } from "./getLength";
export var withMaxLength = function (editor) {
    var e = editor;
    var apply = e.apply;
    e.apply = function (op) {
        var maxLength = e.maxLength, children = e.children;
        var leftLength = 0;
        var currentLength = 0;
        if (op.type !== "set_selection") {
            e.noSelection = undefined;
        }
        switch (op.type) {
            case "insert_node":
                if (maxLength) {
                    leftLength = getLength(children);
                    currentLength = Node.string(op.node).length;
                    if (leftLength + currentLength > maxLength) {
                        return;
                    }
                }
                apply(op);
                return;
            case "insert_text":
                if (maxLength) {
                    leftLength = getLength(children);
                    if (leftLength + op.text.length > maxLength) {
                        op.text = "";
                    }
                }
                apply(op);
                return;
            default:
                apply(op);
                return;
        }
    };
    return e;
};
