import { Transforms } from "slate";
import { stringToSlateNodeList } from "./stringtoHtml";
/**
 *
 * @param editor
 * @returns
 */
export var withHtml = function (editor) {
    var insertData = editor.insertData;
    editor.insertData = function (data) {
        var string = data.getData("application/x-slate-fragment");
        if (string) {
            var decodeVal = window.decodeURIComponent(window.atob(string));
            var parsed = JSON.parse(decodeVal);
            Transforms.insertFragment(editor, parsed);
            return;
        }
        var html = data.getData("text/html");
        if (html) {
            var fragment = stringToSlateNodeList(html);
            try {
                Transforms.insertFragment(editor, fragment);
            }
            catch (_a) {
                return;
            }
            return;
        }
        insertData(data);
    };
    return editor;
};
