/**
 * @file setSliderLeft
 * @date 2022-02-23
 * @author xuejie.he
 * @lastModify xuejie.he 2022-02-23
 */
/**
 * bar
 * @param {HTMLElement} node bar
 * @param {number} left  
 */
export var setSliderLeft = function (node, left) {
    var bar = node.querySelector("[class*=slider_bar]");
    var progress = node.querySelector("[class*=slider_walked]");
    if (bar) {
        bar.style.transform = "translateX(".concat(left, "px)");
    }
    if (progress) {
        progress.style.width = "".concat(left, "px");
    }
};
