/**
 * @file: Add 0 in front of the single digit
 * @date: 2021-05-21 12:52
 * @author: xuejie.he
 * @lastModify: xuejie.he 2021-05-21 12:52
 */
/**
 *
 * @param {number} length =>  The length to be cut
 * @param {number} val => The value to intercept
 * @returns {string}
 */
export var addZero = function (length, val) {
    var str = "";
    for (var i = 0; i < length; i++) {
        str += "0";
    }
    str += val;
    return str.substring(str.length - length, str.length);
};
/**
 *
 */
export function customDate(formatStr, date) {
    var unit = "";
    var t = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "H+": date.getHours(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(), // second
    };
    var reg = /y+/i;
    // year
    if (reg.test(formatStr)) {
        formatStr = formatStr.replace(reg, function (res) {
            var y = date.getFullYear().toString();
            return y.substring(4 - res.length);
        });
    }
    var _loop_1 = function (a) {
        if (new RegExp("(".concat(a, ")")).test(formatStr)) {
            reg = new RegExp("(".concat(a, ")"));
            var val_1 = Number(t[a]);
            switch (a) {
                case "h+":
                    if (val_1 >= 12) {
                        unit = "PM";
                        val_1 -= 12;
                    }
                    else {
                        unit = "AM";
                    }
                    formatStr = formatStr.replace(reg, function () {
                        return addZero(2, val_1);
                    });
                    break;
                default:
                    formatStr = formatStr.replace(reg, function () {
                        return addZero(2, val_1);
                    });
                    break;
            }
        }
    };
    // other (month || day)
    for (var a in t) {
        _loop_1(a);
    }
    return "".concat(formatStr).concat(unit);
}
/**
 *
 * @param {dateType} year
 * @param {dateType} month
 * @param {dateType} day
 * @param {string} customDateFormat
 * @returns {string}
 */
export function dateFormat(year, month, day, customDateFormat) {
    return customDate(customDateFormat, new Date(Number(year), Number(month) - 1, Number(day)));
}
