import axios from "axios";
import { getLanguage } from "../../../Unit/getPosition";
import notice from "../../Feedback/notice";
import mainDomain from "./mainDomain";
import { jwtKey } from "./redirectDomain";
export var service = axios.create({
    baseURL: mainDomain,
    timeout: 10000,
});
//request interceptors
service.interceptors.request.use(function (config) {
    var jwt = window.localStorage.getItem("".concat(jwtKey));
    if (jwt) {
        config.headers["DR-AUTH"] = jwt;
    }
    if (window.navigator.onLine === false) {
        void getLanguage().then(function (language) {
            throw new axios.Cancel(language === "en"
                ? "The network status is not good, please check your network"
                : "");
        });
    }
    return config;
}, function (error) {
    console.log("request error-->", error); // for debug
    return error;
});
//respondent interceptors
service.interceptors.response.use(function (config) {
    return config;
}, function (error) {
    if (axios.isCancel(error)) {
        void getLanguage().then(function (language) {
            error.message &&
                notice.error({
                    title: language === "en" ? "Error" : "",
                    description: error.message,
                });
        });
    }
    else {
        return error.response;
    }
    return error;
});
