import styles from "../style.module.scss";
export var getTransitionClass = function (placement) {
    return {
        enter: {
            active: styles["kite_".concat(placement, "EnterActive")],
            to: styles["kite_".concat(placement, "EnterTo")],
            from: styles["kite_".concat(placement, "EnterFrom")],
        },
        leave: {
            active: styles["kite_".concat(placement, "LeaveActive")],
            to: styles["kite_".concat(placement, "LeaveTo")],
            from: styles["kite_".concat(placement, "LeaveFrom")],
        },
    };
};
